.PageBackground {
    text-align: center;
    background: url('../images/background.jpg');
    height: 100%;
}

.PageBackgroundShadow {
    background: -webkit-gradient(linear, left top, left bottom, from(#0D47A0), to(#0D47A000));
    background: -webkit-linear-gradient(top, #0D47A0, #0D47A000);
    background: -o-linear-gradient(top, #0D47A0, #0D47A000);
    background: linear-gradient(to bottom, #0D47A0, #0D47A000);
    height: 100%;
    color: #ffffff;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
}

.PageHeader {
    height: 130px;
    width: 300px;
    background: url('../images/header-frame.png');
    background-size: 100%;
    background-repeat: no-repeat;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}



